.border {
    border: 1px solid lightgray;
    border-radius: 10px;
    margin-top: 5px;
    padding: 5px;
}

.flexRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    border: 1px solid lightgray;
    border-radius: 5px;
    padding: 5px;
}

.country {
    width: 120px;
    min-height: 120px;

    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.countryName {
    text-align: center;
}
